import React from 'react'
import './Blog.css'

function Blog() {
    return (
        <div>


       
            <div className="blog">
                <video className="blog-video" src="https://css-tricks-post-videos.s3.us-east-1.amazonaws.com/Island%20-%204141.mp4" autoPlay={true} muted loop> </video>
</div>

 
                <header class="viewport-header">
  <h1>
    Explore 
    <span> <i><i class="fas fa-plane-departure"></i></i> Adventure <i><i class="fas fa-globe"></i></i></span>
  </h1>


</header>    
<div className="container">
  <div className="images">
    <img className=" im" src="images/blog-1.jpg" alt="" />
   <p>Travel blogging is a popular niche. If you are looking to create your own WordPress travel website, this can play to your favor, as there are so many travel blogs to gather inspiration from.

In this article, we will look at a number of travel blogs (in no particular order) created with WordPress. Hopefully, these sites will help inspire the design, layout, navigation, pages, images, sidebars, widgets, and many other aesthetics and features, of your own travel blog. So let’s get started</p>
  </div>


  <div className="images">
    <img className=" ims" src="images/blog-2.jpg" alt="" />
  <p>Migrationoloy is a travel food blog that is well laid out, organized, and very easy to navigate. The homepage uses full-screen video media to appeal to fellow or wanna-be travelers, and it works. The rest of the homepage is divided into sections, displaying full-width images with text overlay, a search tool, latest blog articles, and ‘Join Me’ buttons leading to an email signup form.

A clear grid style is used to display the blog’s articles. Individual posts consist of a multitude of relevant photographs. Guides for a selection of countries also list information in a clear format, with large images and links to hotels, restaurants, and further travel info.</p>
  </div>
  

  <div className="images">
    <img className=" ims" src="images/blog-3.jpg" alt="" />
    <p>The Poor Traveler uses a full-screen slider that displays awe-inspiring images which will immediately connect with wanna-be travelers. An Agoda search bar sits at the bottom of this slider. This helps visitors find accommodation in their chosen city, which in turn, aids the monetization of the blog.

Social media plays an important role for this travel blog. Social media buttons are shown, including the number of current followers. An impressive Instagram feed is also displayed on every page, as well as underneath each blog post.</p>

    </div>


</div>


            </div>





        
    )
}

export default Blog;
